import type { ComponentType, FC, RefObject } from 'react'

import type { DispatchProps } from '../reducer'
import type { State } from '../types'
import { EditorSidebar } from '../components/EditorSidebar'
import { EditorChart } from '../components/EditorChart'
import type { APIKeyInputProps } from '../components/APIKeyInput'
import type { CopyLinkButtonProps } from '../components/CopyLinkButton'


export type EditorProps =
    & DispatchProps
    & Omit<State, 'version' | 'viewing'>
    & {
        chartRef: RefObject<HTMLElement>
        keyInputComponent?: ComponentType<APIKeyInputProps>
        copyLinkComponent?: ComponentType<CopyLinkButtonProps>
    }


export const Editor: FC<EditorProps> = ({
    dispatch,
    charts,
    activeChartIndex,
    apiKey,
    searchState,
    screenshotState,
    highlightedID,
    chartRef,
    copyLinkComponent,
    keyInputComponent
}) =>
    <>
        <EditorSidebar dispatch={dispatch}
            charts={charts}
            activeChartIndex={activeChartIndex}
            apiKey={apiKey}
            searchState={searchState}
            screenshotState={screenshotState}
            chartRef={chartRef}
            copyLinkComponent={copyLinkComponent}
            keyInputComponent={keyInputComponent}/>
        <EditorChart {...charts[activeChartIndex]!}
            ref={chartRef}
            dispatch={dispatch}
            highlighted={highlightedID}/>
    </>
